<template>
  <div class="container" @click="cls()">
    <div class="wrapper">
        <swiper :options="swiperOptions">
          <!-- slides -->
          <swiper-slide v-for="(item,index) in imgList" :key="index">
              <img class="gallary-img" :src="item">
          </swiper-slide>
          <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
     </div>
  </div>
</template>

<script>
import Bus from 'bus'
export default {
  name: 'Gallery',
  data () {
    return {
      imgList: [],
      swiperOptions: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observer: true,
        observeParents: true
      }
    }
  },
  created () {
    Bus.$on('showImgData', this.imgConversion)
  },
  methods: {
    imgConversion (e) {
      this.imgList = e
    },
    cls () {
      Bus.$emit('showImgState', false)
    }
  }
}
</script>

<style lang='stylus' scoped>
.container >>> .swiper-container
  overflow: inherit
.container
  display flex
  flex-direction: column
  justify-content: center
  z-index: 99
  position: fixed
  left: 0
  right: 0
  top: 0
  bottom: 0
  background: #000
  .wrapper
    height: 0
    width: 100%
    padding-bottom: 100%
    .gallary-img
      width: 100%
    .swiper-pagination
      bottom: -1rem
      color: #fff
</style>



